import styled from "styled-components";
export const LongforItemWrapper = styled.div`
  width: 20%;
  flex-shrink: 0;
  box-sizing: border-box;
  padding: 0 8px;
  .inner {
    position: relative;
    cursor: pointer;
  }
  .radius {
    border-radius: 8px;
    overflow: hidden;
  }
  .cover {
    width: 100%;
  }
  .bg-cover {
    position: absolute;
    border-radius: 8px;
    bottom: 0;
    height: 60%;
    width: 100%;
    background-image: linear-gradient(
      -180deg,
      rgba(0, 0, 0, 0) 3%,
      rgb(0, 0, 0) 100%
    );
    border-radius: 50px, 50px;
  }
  .info {
    position: absolute;
    top: 65%;
    color: #fff;
    width: 100%;
  }
  .city {
    font-size: 16px;
    font-weight: 600;
  }
  .price,
  .city {
    text-align: center;
    margin: 5px;
  }
`;
