import PropTypes from "prop-types";
import React, { memo } from "react";

import SectionHeader from "@/components/section-header";
import RoomsList from "@/components/section-list";
import { RoomsSectionV1Wrapper } from "./style";
import SectionFooter from "@/components/section-footer";

const RoomsSectionV1 = memo((props) => {
  const { roomsInfo } = props;
  return (
    <RoomsSectionV1Wrapper>
      <SectionHeader title={roomsInfo.title} subtitle={roomsInfo.subtitle} />
      <RoomsList roomsInfo={roomsInfo.list} />
      <SectionFooter />
    </RoomsSectionV1Wrapper>
  );
});

RoomsSectionV1.propTypes = {
  roomsInfo: PropTypes.object,
};

export default RoomsSectionV1;
