import styled from "styled-components";
export const ControlTabsWrapper = styled.div`
  margin: 20px 0;
  .addressButton {
    flex-basis: 120px;
    flex-shrink: 0;
    font-size: 16px;
    text-align: center;
    border: 1px #d8d8d8 solid;
    border-radius: 3px;
    padding: 14px 16px;
    box-sizing: border-box;
    margin-right: 20px;
    white-space: nowrap;
    cursor: pointer;
    ${(props) => props.theme.mixin.boxShadow}
  }

  .active {
    background-color: #00848a;
    color: #fff;
  }
`;
