import styled from "styled-components";
export const ScrollViewWrapper = styled.div`
  position: relative;
  .content {
    overflow: hidden;
  }
  .arrayRight,
  .arrayLeft {
    cursor: pointer;
    background-color: #fff;
    border: 1px solid #e0e0e0;
    padding: 7px;
    border-radius: 50%;
    position: absolute;
    z-index: 2;
    ${(props) => props.theme.mixin.boxShadow}
  }
  .arrayLeft {
    left: -14px;
    top: 50%;
    transform: translate(0, -50%);
  }
  .arrayRight {
    right: -14px;
    top: 50%;
    transform: translate(0, -50%);
  }
  .scrollContent {
    display: flex;
    transition: transform 250ms ease;
    :last-child {
      margin-right: 0;
      padding-right: 0;
    }
  }
`;
