import RoomItem from "@/components/room-item";
import ScrollView from "@/base-ui/scroll-view";
import SectionFooter from "@/components/section-footer";
import SectionHeader from "@/components/section-header";
import PropTypes from "prop-types";
import React, { memo } from "react";
import { RoomsPlusWrapper } from "./style";

const RoomsPlus = memo((props) => {
  const { roomsInfo } = props;
  return (
    <RoomsPlusWrapper>
      <SectionHeader title={roomsInfo.title} subtitle={roomsInfo.subtitle} />
      <div className="plusList">
        <ScrollView>
          {roomsInfo.list.map((item) => (
            <RoomItem key={item.id} roomData={item} itemWidth={"20%"} />
          ))}
        </ScrollView>
      </div>
      <SectionFooter addressName={"PLUS"} />
    </RoomsPlusWrapper>
  );
});

RoomsPlus.propTypes = {
  roomsInfo: PropTypes.object,
};

export default RoomsPlus;
