import classNames from "classnames";
import PropTypes from "prop-types";
import React, { memo, useState } from "react";
import ScrollView from "../../base-ui/scroll-view";
import { ControlTabsWrapper } from "./style";

const ControlTabs = memo((props) => {
  const { addressName = [], tabsClick } = props;
  const [activeIndex, setActiveIndex] = useState(0);
  function tabsClickHander(name, index) {
    setActiveIndex(index);
    tabsClick(name);
  }
  return (
    <ControlTabsWrapper>
      <ScrollView>
        {addressName.map((item, index) => {
          return (
            <div
              key={item}
              onClick={(e) => {
                tabsClickHander(item, index);
              }}
              className={classNames("addressButton", {
                active: index === activeIndex,
              })}
            >
              {item}
            </div>
          );
        })}
      </ScrollView>
    </ControlTabsWrapper>
  );
});

ControlTabs.propTypes = {
  addressName: PropTypes.array,
};

export default ControlTabs;
