import LongforItem from "@/components/longfor-item";
import ScrollView from "@/base-ui/scroll-view";

import SectiontHeader from "@/components/section-header";
import PropTypes from "prop-types";
import React, { memo } from "react";
import { RoomsLongforWrapper } from "./style";

const RoomsLongfor = memo((props) => {
  const { roomsInfo } = props;
  return (
    <RoomsLongforWrapper>
      <SectiontHeader title={roomsInfo.title} subtitle={roomsInfo.subtitle} />
      <div className="locationList">
        <ScrollView>
          {roomsInfo.list.map((item, index) => (
            <LongforItem locationInfo={item} key={index} />
          ))}
        </ScrollView>
      </div>
    </RoomsLongforWrapper>
  );
});

RoomsLongfor.propTypes = {};

export default RoomsLongfor;
