import ControlTabs from "@/components/section-tabs";
import RoomsList from "@/components/section-list";
import SectionFooter from "@/components/section-footer";
import SectiontHeader from "@/components/section-header";
import PropTypes from "prop-types";
import React, { memo, useCallback, useState } from "react";
import { RoomsSectionV2Wrapper } from "./style";
import ScrollView from "@/base-ui/scroll-view";

const RoomsSectionV2 = memo((props) => {
  const { roomsInfo } = props;
  const addressName = roomsInfo.dest_address?.map((item) => item.name);
  const initialAddress = Object.keys(roomsInfo.dest_list)[0];
  const [address, setAddress] = useState(initialAddress);
  const infoTabs = useCallback(
    function (name) {
      setAddress(name);
    },
    [address]
  );
  return (
    <RoomsSectionV2Wrapper>
      <SectiontHeader title={roomsInfo.title} subtitle={roomsInfo.subtitle} />
      <ControlTabs addressName={addressName} tabsClick={infoTabs} />
      <RoomsList
        roomsInfo={roomsInfo?.dest_list?.[address]}
        itemWidth={"33.3333%"}
      />
      <SectionFooter addressName={address} />
    </RoomsSectionV2Wrapper>
  );
});

RoomsSectionV2.propTypes = {
  roomsInfo: PropTypes.object,
};

export default RoomsSectionV2;
