import { fetchHomeDataAction } from "@/store/modules/home";
import { changeHeaderConfig } from "@/store/modules/main";
import { isEmptyObeject } from "@/utils";
import React, { memo, useEffect } from "react";
import { shallowEqual, useDispatch, useSelector } from "react-redux";

import HomeBanner from "./childComponents/home-banner";
import RoomsLongfor from "./childComponents/rooms-longfor";
import RoomsPlus from "./childComponents/rooms-plus";
import RoomsSectionV1 from "./childComponents/rooms-section-v1";
import RoomsSectionV2 from "./childComponents/rooms-section-V2";
import { HomepageWrapper } from "./style";
const Home = memo(() => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(fetchHomeDataAction("xxxx"));
    dispatch(changeHeaderConfig({ isFixed: true }));
  }, [dispatch]);
  const {
    goodPriceInfo,
    highScoreInfo,
    discountInfo,
    hotRecommendestInfo,
    longforInfo,
    plusInfo,
  } = useSelector(
    (state) => ({
      goodPriceInfo: state.home.goodPriceInfo,
      highScoreInfo: state.home.highScoreInfo,
      discountInfo: state.home.discountInfo,
      hotRecommendestInfo: state.home.hotRecommendestInfo,
      longforInfo: state.home.longforInfo,
      plusInfo: state.home.plusInfo,
    }),
    shallowEqual
  );
  return (
    <HomepageWrapper>
      <HomeBanner />
      <div className="contentWrapper">
        {isEmptyObeject(discountInfo) && (
          <RoomsSectionV2 roomsInfo={discountInfo} />
        )}
        {isEmptyObeject(hotRecommendestInfo) && (
          <RoomsSectionV2 roomsInfo={hotRecommendestInfo} />
        )}
        {isEmptyObeject(longforInfo) && (
          <RoomsLongfor roomsInfo={longforInfo} />
        )}
        {isEmptyObeject(goodPriceInfo) && (
          <RoomsSectionV1 roomsInfo={goodPriceInfo} />
        )}
        {isEmptyObeject(highScoreInfo) && (
          <RoomsSectionV1 roomsInfo={highScoreInfo} />
        )}
        {isEmptyObeject(plusInfo) && <RoomsPlus roomsInfo={plusInfo} />}
      </div>
    </HomepageWrapper>
  );
});

export default Home;
