import PropTypes from "prop-types";
import React, { memo } from "react";
import RoomItem from "../room-item";
import { RoomsListWrapper } from "./style";

const RoomsList = memo((props) => {
  const { roomsInfo = [], itemWidth } = props;
  return (
    <RoomsListWrapper>
      {roomsInfo.slice(0, 8).map((item) => (
        <RoomItem roomData={item} key={item.id} itemWidth={itemWidth} />
      ))}
    </RoomsListWrapper>
  );
});

RoomsList.propTypes = {
  roomsInfo: PropTypes.array,
};

export default RoomsList;
