import IconArrowLeft from "@/assets/svg/icon-arrow-left";
import IconArrowRight from "@/assets/svg/icon-arrow-right";
import PropTypes from "prop-types";
import React, { memo, useRef, useEffect, useState } from "react";
import { ScrollViewWrapper } from "./style";

const ScrollView = memo((props) => {
  const scrollContentRef = useRef();
  const [rightShow, setRightShow] = useState(true);
  const [leftShow, setLeftShow] = useState(false);
  const [positionIndex, setPositionIndex] = useState(0);
  const totalDistance = useRef();
  function arrayClick(isRight) {
    const newIndex = isRight ? positionIndex + 1 : positionIndex - 1;
    const newEle = scrollContentRef.current.children[newIndex];
    const newEleLeft = newEle.offsetLeft;
    scrollContentRef.current.style.transform = `translate(${-newEleLeft}px)`;
    setPositionIndex(newIndex);
    setRightShow(newEleLeft < totalDistance.current);
    setLeftShow(newEleLeft > 0);
  }
  useEffect(() => {
    const scrollWidth = scrollContentRef.current.scrollWidth;
    const currentWidth = scrollContentRef.current.clientWidth;
    totalDistance.current = scrollWidth - currentWidth;
    if (totalDistance.current > 0) {
      setRightShow(true);
    }
  }, [props.children]);
  return (
    <ScrollViewWrapper>
      {rightShow && (
        <div className="arrayRight" onClick={(e) => arrayClick(true)}>
          <IconArrowRight size={{}} />
        </div>
      )}
      {leftShow && (
        <div className="arrayLeft" onClick={(e) => arrayClick(false)}>
          <IconArrowLeft size={{}} />
        </div>
      )}
      <div className="content">
        <div className="scrollContent" ref={scrollContentRef}>
          {props.children}
        </div>
      </div>
    </ScrollViewWrapper>
  );
});

ScrollView.propTypes = {};

export default ScrollView;
